	cursor: pointer;
}

.awsm-acc-head h3 {
	margin: 0;
	position: relative;
	padding-left: 15px;
}

.awsm-acc-head h3::before {
	content: "";
	border-width: 6px 5px 0;
	border-style: solid;
	border-color: #000 transparent transparent;
	position: absolute;
	left: 0;
	top: calc(50% - 3px);
}

.awsm-acc-head.on h3::before {
	border-width: 0 5px 6px;
	border-color: transparent transparent #000;
}

