.awsm-toggle-switch {
	position: absolute;
	right: 20px;
	top: calc(50% - 8px);
	height: 16px;
}

.awsm-toggle-switch .awsm-ts-inner {
	width: 25px;
	height: 16px;
	border-radius: 10px;
	background: #cfcfcf;
	position: relative;
}

.awsm-toggle-switch input {
	position: absolute;
	left: 0;
	top: 0;
	opacity: 0;
}

.awsm-toggle-switch span {
	display: inline-block;
	vertical-align: middle;
}

.awsm-toggle-switch .awsm-ts-label::before {
	content: attr(data-off);
	font-weight: 500;
}

.awsm-toggle-switch input:checked~.awsm-ts-label::before {
	content: attr(data-on);
}

.awsm-toggle-switch .awsm-ts-inner::before {
	content: "";
	width: 12px;
	height: 12px;
	background: #fff;
	border-radius: 6px;
	position: absolute;
	left: 2px;
	top: 2px;
}

.awsm-toggle-switch input:checked~.awsm-ts-inner {
	background: #82c438;
}

.awsm-toggle-switch input:checked~.awsm-ts-inner::before {
	left: 11px;
}

